import {Component, View, CORE_DIRECTIVES, ElementRef, Input, Output, EventEmitter} from 'angular2/angular2';

@Component({
    selector: 'alert'
})
@View({
    styles: [`
      .alertFadeIn {
        -webkit-animation-name: fadeIn;
        -moz-animation-name: fadeIn;
        animation-name: fadeIn;
        -webkit-animation-duration: 1s;
        -moz-animation-duration: 1s;
        animation-duration: 1s;
        -webkit-animation-timing-function: ease;
        -moz-animation-timing-function: ease;
        animation-timing-function: ease; }
    `],
    template: `
      <div
          *ng-if="displayed"
          role="alert"
          class="alert alertFadeIn"
          [ng-class]="{'alert-success': type === 'success', 'alert-info': type === 'info', 'alert-warning': type === 'warning', 'alert-danger': type === 'danger' }" >
          <button *ng-if="closeButton" (click)="close()" type="button" class="close" aria-label="Close">
              <span aria-hidden="true">&times;</span>
              <span class="sr-only">Close</span>
          </button>
          <ng-content></ng-content>
      </div>
    `,
    directives: [CORE_DIRECTIVES]
})
export class Alert {
    private _el:HTMLElement;
    @Input() displayed: boolean = false;
    @Input() closeButton: boolean = true;
    @Input() type: string = 'success';
    @Output() alertDisplayedChange:EventEmitter = new EventEmitter();

    constructor(el: ElementRef){
        this._el = el.nativeElement;
    }

    getElement(): HTMLElement{
        return this._el;
    }

    close():void{
        this.displayed = false;
        this.alertDisplayedChange.next(null);
    }
}

export var ALERT_PROVIDERS = [
    Alert
];